.header {
    position: fixed;
    font-size: p2r(16);
    left: 0;
    right: 0;
    z-index: 1000;
    transition: background-color 0.3s;
    box-shadow: 0 0 0 p2r(128) rgba(38, 51, 39, 0.01) inset;
    width: 100vw;

    &-main {
        position: relative;
    }

    &__light {
        background-color: $bg_article;        
    }

    &.black {        
        .header-nav > ul > li > a {
            color: $font_black;

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    color: $font_accent;		
                }
            }
        }

        .header-nav-icon {
            stroke: $primary;
        }
    }

    &-blur {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        z-index: 5;
        height: p2r(120);  
        transform-origin: left top;
        transform: translateZ(0) translateY(-25%);

        

        @include tablet {
            height: p2r(80);
        }

        @include mobile {
            height: p2r(62);
        }
  
        & > div {
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
  
          &:nth-of-type(1) {
            z-index: 2;
            backdrop-filter: blur(p2r(1));
            mask-image: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, rgb(255, 255, 255) 12.5%, rgb(255, 255, 255) 37.5%, rgba(255, 255, 255, 0) 50%);
            filter: brightness(91%);  
          }
  
          &:nth-of-type(2) {
            z-index: 3;
            backdrop-filter: blur(p2r(3));
            mask-image: linear-gradient(to top, rgba(255, 255, 255, 0) 12.5%, rgb(255, 255, 255) 37.5%, rgb(255, 255, 255) 50%, rgba(255, 255, 255, 0) 62.5%);
            filter: brightness(90%);
          }
  
          &:nth-of-type(3) {
            z-index: 4;   
            backdrop-filter: blur(p2r(4));
            mask-image: linear-gradient(to top, rgba(255, 255, 255, 0) 37.5%, rgb(255, 255, 255) 50%, rgb(255, 255, 255) 62.5%, rgba(255, 255, 255, 0) 75%);
            filter: brightness(89%);
          }
  
          &:nth-of-type(4) {
            z-index: 5;   
            backdrop-filter: blur(p2r(6));
            mask-image: linear-gradient(to top, rgba(255, 255, 255, 0) 50%, rgb(255, 255, 255) 62.5%, rgb(255, 255, 255) 75%, rgba(255, 255, 255, 0) 87.5%);
            filter: brightness(96%);      
          }
        }
    }

    // &.hidden {
    //     z-index: 1003;

    //     .header-logo, .header-nav, .header-search, .header-feedback, .header-blur {
    //         opacity: 0;
    //         z-index: -1;
    //         visibility: hidden;
    //     }
        
    //     .header-logo, .header-blur {
    //         @include tablet {
    //             opacity: 1;
    //             visibility: visible;
    //         }
    //     }
    // }

    &-wrapper {
        position: relative;
        display: flex;
        align-items: center;
        padding-top: p2r(16);
        padding-bottom: p2r(16);
        z-index: 6;

        @include tablet {
            padding-top: p2r(4);
            padding-bottom: 0;
        }

        @include mobile {           
            padding-bottom: 0;
        }
    }

    &-logo {  
        display: block;
        margin-right: p2r(28);
        transition: opacity 0.3s;
        position: relative;
        z-index: 2000;
        line-height: 1;

        img {
            width: p2r(77.5);
            height: auto;
            display: block;

            @include mobile {
                width: p2r(70);               
            }
        }

        @media (hover: hover) and (pointer: fine) {
            &:not(span):hover {
                opacity: 0.5;
            }
        }
        
        @include tablet {
            margin-right: auto;
        }        
    }

    &-nav {
        font-size: p2r(16);

        @include tablet {
            display: none;
        }

        @include mobile {
            font-size: p2r(14);
        }
        
        & > ul {
            display: flex;

            & > li {
                position: relative;
                display: block;

                &:not(:last-child) {
                    margin-right: p2r(21);

                    @include tablet {
                        margin-right: p2r(32);
                    }

                    @include mobile {
                        margin-right: p2r(20);
                    }
                }

                &.active {                  
                   
                    & > a {
                        position: relative;
                        color: $font_accent;
                        border-radius: p2r(40);
                        // cursor: default;

                        &::before {
                            content: '';
                            position: absolute;
                            top: p2r(-8);
                            left: p2r(-12);                            
                            height: p2r(40);
                            width: 126%;
                            width: calc(100% + 2rem);
                            border-radius: p2r(40);
                            background-color: rgba(16, 51, 43, 0.5);
                            backdrop-filter: blur(p2r(32));                           
                            z-index: -1;
                            display: block;
                            background-repeat: repeat;
                            background-size: 100px 100px;	
                            background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.5' numOctaves='1.5' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%' height='100%' filter='url(%23noiseFilter)' opacity='0.14'/%3E%3C/svg%3E");
                        }
                    }
                    
                    .header-nav-menu {
                        display: flex;             
                    }

                    .header-nav-icon {
                        stroke: $font_accent;             
                    }
                }

                & > a {
                    color: $font_white;
                    transition: color 0.3s;
                    // padding: p2r(12) p2r(18);
                    display: block;

                    &.active {
                        color: $font_accent;

                        .header-nav-icon {
                            stroke: $font_accent;
                        }
                    }
    
                    @media (hover: hover) and (pointer: fine) {           
                        &:hover {
                            color: $font_accent;
        
                            .header-nav-icon {
                                stroke: $font_accent;  
                            }
                        }
                    }                
                }
            }

            
        }

        &-icon {
            width: p2r(8);
            height: p2r(8);
            fill: none;                
            stroke: $font_white;
            margin-left: p2r(4);
            transition: stroke 0.3s;
        }

        &-menu {
            background: rgba(11, 11, 11, 0.7);            
            display: none;
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: -2;

            &:not(.header-nav-menu__simple) {
                width: 100vw;
            }
            
            &__simple {
                position: static;
                background: unset;

                &::before {
                    content: '';
                    background: rgba(11, 11, 11, 0.7);                   
                    position: fixed;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    z-index: -2;
                }
            }
            
            &-inner {                 
                display: flex;
                max-width: p2r(1302);
                // background: $bg;
                max-height: calc(100vh - 88px);
                border-radius: p2r(40);
                margin-left: auto;
                margin-right: auto;
                margin-top: p2r(72);
                margin-bottom: p2r(16);
                background-color: rgba(16, 51, 43, 0.5);
                backdrop-filter: blur(p2r(32));

                &__simple {
                    position: absolute;
                    left: p2r(-32);
                    top: p2r(32);
                    margin-top: p2r(16);
                    padding-left: p2r(32);
                    padding-right: p2r(32);
                    min-width: p2r(193);

                    ul {
                        li {
                            margin-bottom: 16px;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }

                    a {
                        color: $font_white;

                        @media (hover: hover) and (pointer: fine) {
                            &:hover {
                                color: $font_accent;
                            }
                        }
                    }
                }

                @include tablet {
                    &:not(.popup-selection-inner) {
                        display: none;
                    }
                    
                }
            }
           

            ul {
                display: flex;
                flex-direction: column;
            }
        }

        &-submenu {
            display: none;
            position: absolute;
            left: p2r(320);            

            &.active {
                display: flex;
            }
        }
    }

    &-feedback {
        margin-left: auto;        

        @include tablet {
            margin-left: p2r(24);
        }

        @include mobile {
            margin-left: p2r(20);
        }

        &-item {
            display: inline-block;
            vertical-align: baseline;
        }

        &-phone {
            margin-right: p2r(23);
            position: relative;
            transition: color 0.3s;

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    color: $font_white;
    
                    .header-feedback-icon {
                        // filter: brightness(10);
                        stroke: $font_white;
                    }
                }
            }

            @include tablet {
                margin-right: 0;
                display: flex;
                z-index: -1;
            }
            
            &::after {
                position: absolute;
                top: 50%;
                right: p2r(-12);
                transform: translate(50%, -50%);
                content: '';
                width: p2r(4);
                height: p2r(4);
                background-color: $font_accent;
                border-radius: 50%;

                @include tablet {
                    display: none;
                }
            }
        }

        &-icon {
            width: p2r(16);
            height: p2r(16);
            fill: none;
            stroke: $font_accent;           
            display: inline-block;
            vertical-align: middle;
            margin-right: p2r(1);
            transition: filter 0.3s, stroke 0.3s;
            margin-bottom: p2r(2);

            @include tablet {
                width: p2r(24);
                height: p2r(24);
                margin-right: 0;
                margin-bottom: 0;
                margin-bottom: p2r(2);
                // filter: brightness(10);    
                stroke: $font_white;           
            }

            @include mobile {
                width: p2r(18);
                height: p2r(18);
                margin-bottom: p2r(3);
            }
        }
    }

    &-burger {
        position: relative;
        background-color: transparent;      
        flex-basis: p2r(16);    
        max-width: p2r(16);
        min-width: p2r(16);
        height: p2r(16);
        border: none;
        cursor: pointer;
        margin-right: p2r(12);
        outline: none;

        @include tablet {
            order: 1;
            margin-right: 0;
            margin-left: p2r(24);
            margin-top: p2r(-2);
        }

        @include mobile {            
            margin-left: p2r(18);          
        }

        span {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: p2r(16);
      
            &:before,
            &:after {
              content: "";
              display: block;
              width: p2r(16);
              height: p2r(1.5);
              border-radius: p2r(2);
              background-color: $bgc;     
              position: absolute;
              left: 0;
              transition: transform 0.2s ease-in-out, background-color 0.3s;
            }
            &:before {
              top: p2r(-4);             
            }
            &:after {
                top: p2r(4);         
            }
      
            // &.active {
            //   transform: translate(-50%, -50%) rotate(45deg);       
             
            //   &:before {
            //     top: 0;
            //     transform: rotate(0deg);
            //     width: p2r(20);
            //   }
            //   &:after {
            //     top: 0;
            //     transform: rotate(90deg);
            //     width: p2r(20);
            //   }
            // }            
        }

        @media (hover: hover) and (pointer: fine) {
            &:hover {           
                span {
                    &::before, &::after {
                        background-color: $font_accent;
                    }
                }
            }
        }

        &-menu {
            display: none;
            justify-content: center;
            align-items: center;
            // gap: 124px;         
            overflow-y: auto;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 1001;
            background-color: rgba(16, 51, 43, 0.7);
            backdrop-filter: blur(p2r(16));

            @include tablet {
                justify-content: flex-start;
                align-items: flex-start;
                padding-top: p2r(16);
                padding-left: p2r(16);
                padding-right: p2r(16);
                height: calc(var(--vh, 1vh) * 100);                         
            }

            &.show {
                display: flex;
            }

            &-head {         
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;

                @include mobile {
                    margin-bottom: p2r(10);
                }

                .header-logo {
                    &.hidden {
                        opacity: 0;
                        visibility: hidden;
                    }
                }

                .close {      
                    position: absolute;   
                    left: p2r(80);
                    top: p2r(26); 
                    z-index: 99;
                    border: none;
                    outline: none;
                    background-color: transparent;
                    width: p2r(16);
                    height: p2r(16);
                    cursor: pointer;
                    margin-left: auto;

                    @media (hover: hover) and (pointer: fine) {
                        &:hover {
                            .icon {
                                path {
                                    stroke: $font_accent;
                                }
                            }
                        }
                    }

                    @include tablet {
                        position: relative; 
                        left: unset;
                        top: unset;
                    }
                    
                    .icon {
                      width: p2r(16);
                      height: p2r(16);
                      transition: filter 0.15s;
                      path {
                        transition: stroke 0.3s;
                      }
                    }
                }
            }

            &-wrapper {
                justify-content: center;
                align-items: center;
                gap: p2r(124);      
                display: flex;
                padding-top: p2r(40);
                padding-bottom: p2r(40);

                @include tablet {
                    flex-direction: column;
                    gap: 0;
                    padding-top: p2r(0);
                    width: 100%;
                }

                & > ul {
                    font-size: p2r(32);
                    line-height: 0.85;
                    font-family: $second_font;
                    color: $font_white;                   
                    min-width: p2r(289);
                    flex: 0 0 p2r(289);

                    @include tablet {
                        font-size: p2r(24);
                        width: 100%;
                        flex: 0 0 100%;
                    }

                    @include mobile {
                        font-size: p2r(20);       
                    }

                    &:last-child {
                        & > li {
                            &:last-child {
                                @include tablet {
                                    border-bottom: none;
                                }
                            }                            
                        }
                    }
    
                    & > li {
                        margin-bottom: p2r(32);
                        width: max-content;

                        @include tablet {
                            width: 100%;
                            margin-bottom: 0;
                            padding-top: p2r(22);
                            padding-bottom: p2r(22);
                            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                        }

                        @include mobile{
                            padding-top: p2r(14);
                            padding-bottom: p2r(14);
                        }
    
                        &:last-child {
                            margin-bottom: 0;
                            
                            // @include tablet {
                            //     border: none;
                            // }
                        }
    
                        & > a {
                            color: $font_white;
                        }
    
                        @media (hover: hover) and (pointer: fine) {
                            &:hover {
                                & > a {
                                    color: $font_accent;
                                }
                            }
                        }
    
                        &.active {                           

                            & > a {
                                color: $font_accent;
                            }
    
                            & > ul {
                                display: block;

                                @include tablet {
                                    &:has(li.active) {
                                        margin-top: 0;
                                    }
                                }
                            }
    
                            &:has(ul) {
                                & > a {
                                    
                                    @include tablet {
                                        padding-left: p2r(52);
                                    }
        
                                    &::after {
                                        transform: translateY(-50%) rotate(180deg) !important;

                                        @include tablet {
                                            left: 0;
                                            transform: translateY(-50%) rotate(90deg) !important;
                                        }
                                    }
                                }
                            }

                            @include tablet {
                                border: none;
                                margin-top: p2r(-56);

                                &:has(ul li.active) {
                                    & > a {
                                        display: none;
                                    }
                                }
                            }

                            @include mobile {
                                margin-top: p2r(-50);
                            }
                        }
    
                        &:has(ul:not(.hidden-desktop) ) {
                            
                            & > a {
                                position: relative;
    
                                &::after {
                                    position: absolute;
                                    top: 50%;
                                    right: p2r(-56);
                                    transform: translateY(-50%);
                                    content: '';
                                    width: p2r(32);
                                    height: p2r(32);
                                    border-radius: 50%;
                                    background-color: #10211D;
                                    background-image: url('data:image/svg+xml,<svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 7C8.30831 5.11277 10.2165 1.79112 14 1" stroke="%23E9B566"/><path d="M8 7C7.69169 5.11277 5.78351 1.79112 2 1" stroke="%23E9B566"/></svg>');
                                    background-repeat: no-repeat;
                                    background-position: center;
                                    transition: transform 0.3s;

                                    @include tablet {
                                        right: 0;
                                        transform: translateY(-50%) rotate(-90deg);
                                    }

                                    @include mobile {                                    
                                        width: p2r(24);
                                        height: p2r(24);
                                        background-size: p2r(12);
                                    }
                                }
                            }
                        }

                        &:has(ul.hidden-desktop) {
                            & > a {
                                   
                                &::after {
                                   display: none;

                                   @include tablet {
                                        display: block;                                        
                                   }
                                }
                            }
                        }

                        &:has(ul) {
                            @include tablet {
                                & > a {
                                    position: relative;
                                    display: block;
        
                                    &::after {                                       
                                        position: absolute;
                                        top: 50%;
                                        right: 0;
                                        transform: translateY(-50%) rotate(-90deg);
                                        content: '';
                                        width: p2r(32);
                                        height: p2r(32);
                                        border-radius: 50%;
                                        background-color: #10211D;
                                        background-image: url('data:image/svg+xml,<svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 7C8.30831 5.11277 10.2165 1.79112 14 1" stroke="%23E9B566"/><path d="M8 7C7.69169 5.11277 5.78351 1.79112 2 1" stroke="%23E9B566"/></svg>');
                                        background-repeat: no-repeat;
                                        background-position: center;
                                        transition: transform 0.3s;
                                    }
                                }
                            }
                            
                            
                        }
    
                        & > ul {
                            display: none; 
                            margin-top: p2r(16);                       
                            font-family: $basefont;
                            font-size: p2r(16);
                            line-height: 1.2;
                            color: $font_white;
                            padding-left: p2r(24);

                            @include tablet {
                                padding-left: 0;
                                margin-top: p2r(24);
                            }

                            @include mobile {
                                font-size: p2r(14);
                            }
    
                            & > li {
                                margin-bottom:  p2r(12);

                                @include tablet {
                                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                                    padding-bottom: p2r(16);
                                }
    
                                &:last-child {
                                    margin-bottom: 0;

                                    @include tablet {
                                        border-bottom: none;
                                    }
                                }
    
                                & > a {
                                    color: $font_white;

                                    @include tablet {
                                        display: flex;
                                        align-items: center;                                    
                                    }                                    
                                }
    
                                @media (hover: hover) and (pointer: fine) {
                                    &:hover {
                                        & > a {
                                            color: $font_accent;
                                        }                            
                                    }
                                }
    
                                &.active {

                                    @include tablet {
                                        border: none;
                                        margin-top: p2r(-6);
                                    }

                                    & > a {
                                        color: $font_accent;

                                        @include tablet {
                                            font-size: p2r(24);
                                            font-family: $second_font;                                            

                                            .menu-aside-icon {
                                                display: none;
                                            }

                                            &::after {
                                                order: -1;
                                                transform: rotate(90deg);
                                                margin-left: 0;
                                                background-position: center;
                                                background-image: url('data:image/svg+xml,<svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 7C8.30831 5.11277 10.2165 1.79112 14 1" stroke="%23E9B566"/><path d="M8 7C7.69169 5.11277 5.78351 1.79112 2 1" stroke="%23E9B566"/></svg>');
                                                width: p2r(32);
                                                height: p2r(32);
                                                flex: 0 0 p2r(32);
                                                background-color: #10211D;
                                                border-radius: 50%;
                                                margin-right: p2r(20);

                                                @include mobile {
                                                    width: p2r(24);
                                                    height: p2r(24);
                                                    flex: 0 0 p2r(24);
                                                    background-size: p2r(12) p2r(12);
                                                }
                                            }
                                        }

                                        @include mobile {
                                            font-size: p2r(20);
                                            align-items: flex-start;
                                            max-width: p2r(268);
                                        }
                                    }
                                    
                                    & > ul {
                                        display: block;

                                        @include tablet {
                                            // margin-top: p2r(16);
                                            // margin-top: 0;
                                        }

                                        & > li {
                                            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

                                            &:last-of-type {
                                                border: none;
                                            }

                                            a {
                                                display: flex;
                                                color: $font_white;
                                                padding-top: p2r(16);
                                                padding-bottom: p2r(16);
                                            }

                                            & > ul {
                                                display: none;
                                            }

                                            &.active {
                                                border: none;
                                                margin-top: p2r(-22);

                                                & > ul {
                                                    display: block;
                                                }    
                                                
                                                & > a {
                                                    font-size: p2r(24);
                                                    color: $font_accent;
                                                    font-family: $second_font;

                                                    @include mobile {
                                                        font-size: p2r(20);
                                                    }

                                                    &:before {      
                                                        content: '';
                                                        transform: rotate(90deg);
                                                        margin-left: 0;
                                                        background-position: center;
                                                        background-repeat: no-repeat;
                                                        background-image: url('data:image/svg+xml,<svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 7C8.30831 5.11277 10.2165 1.79112 14 1" stroke="%23E9B566"/><path d="M8 7C7.69169 5.11277 5.78351 1.79112 2 1" stroke="%23E9B566"/></svg>');
                                                        width: p2r(32);
                                                        height: p2r(32);
                                                        flex: 0 0 p2r(32);
                                                        background-color: #10211D;
                                                        border-radius: 50%;
                                                        margin-right: p2r(20);

                                                        @include mobile {
                                                            width: p2r(24);
                                                            height: p2r(24);
                                                            flex: 0 0 p2r(24);
                                                            background-size: p2r(12);
                                                        }
                                                    }
                                                }                                                
                                            }    
                                            
                                            &:not(.active):has(ul) {                                                  
                                                & > a {                                                   
                                                    display: flex;
                                                    justify-content: space-between;

                                                    &:after {
                                                        content: '';
                                                        background-position: center;
                                                        background-repeat: no-repeat;
                                                        background-image: url('data:image/svg+xml,<svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 7C8.30831 5.11277 10.2165 1.79112 14 1" stroke="%23E9B566"/><path d="M8 7C7.69169 5.11277 5.78351 1.79112 2 1" stroke="%23E9B566"/></svg>');
                                                        width: p2r(32);
                                                        height: p2r(32);
                                                        background-color: #10211D;
                                                        border-radius: 50%;
                                                        transform: rotate(-90deg);

                                                        @include mobile {
                                                            width: p2r(24);
                                                            height: p2r(24);
                                                            flex: 0 0 p2r(24);
                                                            background-size: p2r(12) p2r(12);
                                                        }
                                                    }
                                                }

                                                span {
                                                    padding-right: p2r(8);
                                                }
                                            }
                                        }
                                    }

                                    &:has(ul > li.active) {
                                        & > a {
                                            display: none;
                                        }
                                    }
                                }

                                & > ul {
                                    display: none;  
                                    margin-top: p2r(16);
                                    
                                    @include tablet {
                                        &:has(li.active) {
                                            margin-top: p2r(-16);
                                        }
                                    }

                                    li {
                                        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

                                        &:last-child {
                                            border: none;
                                        }                                        
                                    }
                                }
                                
                                &:has(ul) {   
                                    
                                    & > a {
                                        &::after {
                                            content: '';
                                            margin-left: auto;
                                            width: p2r(32);
                                            height: p2r(32);
                                            border-radius: 50%;
                                            background-color: #10211D;
                                            background-image: url('data:image/svg+xml,<svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 7C8.30831 5.11277 10.2165 1.79112 14 1" stroke="%23E9B566"/><path d="M8 7C7.69169 5.11277 5.78351 1.79112 2 1" stroke="%23E9B566"/></svg>');
                                            background-repeat: no-repeat;
                                            background-position: center;
                                            transition: transform 0.3s;                                            
        
                                            @include tablet {
                                                right: 0;
                                                transform: rotate(-90deg);
                                            }
        
                                            @include mobile {                                    
                                                width: p2r(24);
                                                height: p2r(24);
                                                background-size: p2r(12);
                                            }
                                        }
                                    }
                                   
                                }
                            }
                        }
                    }
                }
            }

            
        }
    }

    &-search {
        margin-left: p2r(48);

        &-icon {
            width: p2r(16);
            height: p2r(16);
            display: inline-block;
            stroke: $font_white;
            fill: none;
            transition: stroke 0.3s;
            display: inline-block;
            vertical-align: middle;
            margin-top: p2r(-2);

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    stroke: $font_accent;
                }
            }

            @include tablet {
                width: p2r(24);
                height: p2r(24);
            }

            @include mobile {
                width: p2r(18);
                height: p2r(18);
                margin-top: p2r(-3);
            }
        }
        
       
    }
}

.header-nav-mobile-menu {
    display: none;

    @include tablet {
        overflow-y: auto;
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        padding-top: p2r(74);
        padding-left: p2r(16);
        padding-right: p2r(16);
        padding-bottom: p2r(16);
        background-color: $primary;
        
        // height: 100vh; /* Fallback */
        height: calc(var(--vh, 1vh) * 100);
        // height: -webkit-fill-available;
       

        &.active {
            display: block;
        }
    }

    @include mobile {
        padding-top: p2r(58);
    }
   
}

.mobile {
    &-menu {
        &-item {
            font-size: p2r(16);
            color: $font_white;
            display: flex;
            align-items: center;
            padding-bottom: p2r(16);
            padding-top: p2r(16);
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);        

            &:last-of-type {
                border-bottom: none;
            }

            @include mobile {
                font-size: p2r(14);
                padding-bottom: p2r(14);
                padding-top: p2r(14);
            }

            span {
                padding-right: p2r(52);
            }
        }

        &-arrow {
            width: p2r(16);
            height: p2r(16);
            flex: 0 0 p2r(16);
            fill: none;
            margin-left: auto;           
            stroke: $font_white;
            stroke-width: p2r(0.8);            
        }

        &-back {
            display: flex;
            align-items: center;
            font-weight: 400;
            font-size: p2r(14);
            line-height: 1.2;
            opacity: 0.5;
            padding-bottom: p2r(10);
            margin-bottom: p2r(2);

            @include mobile {
                font-size: p2r(12);
            }

            .mobile-menu-arrow {
                width: p2r(16);
                height: p2r(16);
                stroke: $font_white;
                fill: none;
                stroke-width: p2r(1);
                margin-left: 0;
            }
        }        

        &-header {
            position: relative;
            margin-bottom: p2r(6);
        }

        &-title {
            font-weight: 500;
            font-size: p2r(20);
            line-height: 1.1;
            color: $font_accent;
            margin-bottom: p2r(8);

            @include mobile {
                font-size: p2r(16);
            }
        }

        &-close {
            position: absolute;
            top: p2r(-58);
            right: 0;

            @include tablet {
                top: p2r(-62)
            }

            @include mobile {
                top: p2r(-50);
            }

            img {
                width: p2r(24);
                height: p2r(24);                
                z-index: 99;

                @include tablet {
                    width: p2r(22);
                    height: p2r(22);
                }

                @include mobile {
                    width: p2r(20);
                    height: p2r(20);
                }
            }  
        }
    }    
}