.wrapper {
  position: relative;
  min-height: 100%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.menu-open {
  // height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden !important;

  @include tablet {
    overflow: hidden !important;
  }
}

.non-scroll {
  overflow: hidden !important;
  // height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
}

// -----header-----

// -----main-----
.main {
  flex-grow: 1;
  // background-color: $bg;
  padding-top: p2r(72);
  width: 100vw;

  @include tablet {
    padding-top: p2r(52);
  }

  @include mobile {
    padding-top: p2r(32);
  }

  &-bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -2;
    height: 100vh;
    width: 100%;
    object-fit: cover;
    object-position: top right;
  }

  &-banner {
    min-height: p2r(500);
    height: calc(100vh - 6.301704246rem);

    @include tablet {
      height: calc(100vh - 3.8419677892rem);
    }

    @include mobile {
      height: calc(100vh - 2.7836749634rem);
    }

    &-content {
      position: fixed;
      top: 50%;
      transform: translateY(-50%);
      max-width: p2r(927);

      @include tablet {
        top: unset;
        transform: translateY(0);
        bottom: p2r(24);
      }

      @include mobile {
        bottom: p2r(80);
      }
    }

    &-title {
      font-size: p2r(104);
      margin-bottom: p2r(56);
      line-height: 0.85;

      @include tablet {
        font-size: p2r(64);
        margin-bottom: p2r(32);
      }

      @include mobile {
        font-size: p2r(40);
        margin-bottom: p2r(24);
      }
    }
  }
}

.cookie {
  position: fixed;
  bottom: p2r(24);
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  padding: p2r(8) p2r(8) p2r(8) p2r(24);
  background: rgba(38, 51, 39, 0.25);
  backdrop-filter: blur(p2r(16));
  border-radius: p2r(40);
  font-size: p2r(16);
  width: max-content;
  z-index: 9;

  @include tablet {
    font-size: p2r(14);
  }

  @include mobile {
    font-size: p2r(12);
    max-width: p2r(192);
    flex-direction: column;
    padding: p2r(16);
    border-radius: p2r(24);
  }

  &.hidden {
    display: none;
  }

  &-text {
    margin-right: p2r(16);

    // span {
    //   color: $font_accent;
    // }

    a {
      color: $font_white;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: p2r(10);
      text-align: center;
    }
  }
}

.founders {
  position: relative;
  border-radius: p2r(40);
  height: p2r(680);
  overflow: hidden;
  background-color: #11231F;
  z-index: 1;

  @include tablet {
    width: p2r(568);
    height: p2r(420);
    border-radius: p2r(24);
  }

  @include mobile {
    width: p2r(288);
    height: p2r(420);
    border-radius: p2r(24);
  }

  &-title {
    font-size: p2r(64);
    line-height: 0.85;
    max-width: p2r(460);
    margin-top: p2r(56);
    margin-left: p2r(48);

    @include tablet {
      font-size: p2r(40);
      max-width: p2r(296);
      margin-top: p2r(24);
      margin-left: p2r(16);
    }

    @include mobile {
      font-size: p2r(32);
    }

    &-accent {
      color: $font_accent;
    }
  }

  &-image {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #11231F;
    height: p2r(680);
    width: 100%;
    z-index: -1;
    object-fit: cover;

    @include tablet {
      height: p2r(420);
    }

  }

  &-trigger {
    position: absolute;
    display: flex;
    align-items: center;

    &__left {
      top: p2r(444);
      left: p2r(384);

      @include tablet {
        left: p2r(222);
        top: p2r(275);
      }

      @include mobile {
        left: p2r(44);
        top: p2r(311);
      }

      .founders-trigger-text {
        text-align: right;
      }

    }

    &__right {
      top: p2r(508);
      right: p2r(49);

      @include tablet {
        right: unset;
        left: p2r(490);
        top: p2r(101);
      }

      @include mobile {
        left: p2r(210);
        top: p2r(237);
      }
    }

    &-btn {
      position: relative;
      width: p2r(40);
      height: p2r(40);
      border-radius: 50%;
      background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.4' numOctaves='1.5' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%' height='100%' filter='url(%23noiseFilter)' opacity='0.08'/%3E%3C/svg%3E");
      background-color:  rgba(16, 51, 43, 0.5);
      margin-right: p2r(16);
      margin-left: p2r(16);
      backdrop-filter: blur(p2r(16));
      transition: background-color 0.3s;

      @include tablet {
        width: p2r(32);
        height: p2r(32);
      }

      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background-color:  rgba(16, 51, 43, 1);
        }
      }

      &-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: p2r(16);
        height: p2r(16);
      }
    }

    &-text {
      @include tablet {
        display: none;
      }
    }

    &-surname {
      font-family: $second_font;
      font-weight: 400;
      font-size: p2r(32);
      line-height: 0.85;
      color: $font_accent;
      margin-bottom: p2r(4);
    }

    &-lastname {
      font-size: p2r(20);
    }
  }
}

.period {
  margin-top: p2r(-50);
  position: relative;
  z-index: 1;
  margin-bottom: p2r(166);

  @include tablet {
    margin-top: p2r(-58);
    margin-bottom: p2r(62);
  }

  @include mobile {
    margin-top: p2r(-50);
    margin-bottom: p2r(52);
  }

  &-title {
    position: absolute;
    display: inline-block;
    top: p2r(120);
    right: p2r(24);
    max-width: p2r(620);
    font-family: $second_font;
    font-weight: 400;
    font-size: p2r(88);
    line-height: 0.85;

    @include tablet {
      width: p2r(270);
      height: p2r(136);
      left: p2r(298);
      top: p2r(100);
      font-size: p2r(40);
    }

    @include mobile {
      font-size: p2r(32);
      left: 0;
      top: p2r(100);
    }
  }

  &-link {
    display: block;
    width: max-content;
    margin-top: p2r(40);

    @include tablet {
      margin-top: 0;
      margin-bottom: p2r(16);
    }
  }

  &-benefits {
    padding-left: p2r(58);
    padding-right: p2r(40);

    @include tablet {
      padding-left: p2r(6);
      padding-right: p2r(6);
    }

    @include mobile {
      padding-left: 0;
      padding-right: 0;
    }

    &-item {
      display: flex;
      align-items: center;

      @include tablet {
        margin-bottom: p2r(16);
        height: calc(100% - 1.4055636896rem);
      }

      &-img {
        width: p2r(86);
        height: p2r(86);
        margin-right: p2r(18);
        flex: 0 0 p2r(86);

        @include tablet {
          width: p2r(53);
          height: p2r(53);
          flex: 0 0 p2r(53);
          margin-right: p2r(12);
        }

        @include mobile {
          width: p2r(66);
          height: p2r(66);
          flex: 0 0 p2r(66);
          margin-right: p2r(8);
        }
      }

      &-title {
        font-weight: 400;
        font-size: p2r(20);
        line-height: 1.2;

        @include tablet {
          font-size: p2r(16);
        }
      }
    }
  }
}

.equipment {
  position: relative;
  border-radius: p2r(40);
  margin-bottom: p2r(160);
  width: p2r(1302);
  height: p2r(768);

  @include tablet {
    width: p2r(568);
    height: p2r(404);
    margin-bottom: p2r(80);
  }

  @include mobile {
    width: p2r(288);
    height: p2r(393);
    margin-bottom: p2r(64);
  }


  &-title {
    font-size: p2r(64);
    text-align: center;
    font-size: p2r(72);
    line-height: 0.85;
    margin-top: 0;
    margin-bottom: p2r(-110);

    @include tablet {
      font-size: p2r(40);
      text-align: left;
      margin-bottom: p2r(-64);
      padding-left: p2r(24);
    }

    @include mobile {
      font-size: p2r(32);
      margin-bottom: p2r(-110);
      padding-left: 0;
      position: relative;
      z-index: 1;
    }
  }

  &-link {
    position: absolute;
    width: max-content;
    left: 50%;
    bottom: p2r(44);
    transform: translateX(-50%);

    @include tablet {
