    }
  }

  &-img {
    display: block;
    // width: p2r(1302);
    // height: p2r(768);
    object-fit: cover;
    object-position: center;
    width: 100%;
  }
}

.team {
  position: relative;
  margin-bottom: p2r(160);

  @include tablet {
    margin-bottom: p2r(80);
  }

  @include mobile {
    margin-bottom: p2r(64);
  }

  &-title {
    font-size: p2r(88);
    line-height: 0.85;
    text-align: center;
    margin-bottom: p2r(-90);

    @include tablet {
      font-size: p2r(40);
      margin-bottom: p2r(-46);
    }

    @include mobile {
      font-size: p2r(32);
      text-align: left;
      max-width: p2r(220);
