.svg-container {
  position: relative;
  margin-bottom: p2r(56);

  @include tablet {
    margin-bottom: p2r(20);
  }

  &.desktop-svg {
    @include mobile {
      display: none;
    }
    @include tablet {
      display: none;
    }
  }

  &.tablet-svg {
    display: none;
    @include tablet {
      display: block;
    }
    @include mobile {
      display: none;
    }
  }

  &.mobile-svg {
    display: none;
    @include mobile {
      display: block;
    }
  }
}

.desktop-svg {
  width: p2r(1003);
  height: p2r(547);
}

.tablet-svg {
  width: p2r(444);
  height: p2r(247);
  transform: translate(p2r(10), p2r(-8));
}

.mobile-svg {
  width: p2r(393);
  height: p2r(218);
  transform: translate(p2r(-44), p2r(-8));
}

.blur-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  filter: brightness(94%);
  backdrop-filter: blur(p2r(10));
  // background-color: rgba(47, 61, 48, 0.2);
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.4' numOctaves='1.5' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%' height='100%' filter='url(%23noiseFilter)' opacity='0.08'/%3E%3C/svg%3E");
  background-size: p2r(100) p2r(100);
  z-index: -1;
  background-color: rgba(44, 58, 45, 0.25);

  @include tablet {
    background-color: rgba(16, 51, 43, 0.35);
  }
}

.desktop-svg .blur-background {
  mask-image: url(#svg-mask-desktop);
}

.tablet-svg .blur-background {
  mask-image: url(#svg-mask-tablet);
}

.mobile-svg .blur-background {
  mask-image: url(#svg-mask-mobile);
}


.menu {
  &-aside {
    &-nav {
      font-size: p2r(16);
      width: p2r(264);
      flex: 0 0 p2r(264);
      height: max-content;
      background-color: $primary;
      padding: p2r(24);
      border-radius: p2r(32);
      line-height: 1.2;
      margin-top: p2r(32);
      margin-left: p2r(56);
      margin-right: p2r(56);

      &__mid {
        width: p2r(320);
      }
    }

    &-head {
      margin-top: p2r(32);
      margin-left: p2r(56);
      margin-right: p2r(56);
      margin-bottom: p2r(40);
    }

    &-title {
      font-family: $second_font;
      font-size: p2r(32);
      line-height: 0.85;
      color: $font_accent;
      margin-bottom: p2r(16);
    }

    &-subtitle {
      max-width: p2r(270);
      font-size: p2r(16);
      line-height: 1.4;

      @include tablet {
        line-height: 1.2;
      }
    }

    li {
      &:not(:last-child) {
        margin-bottom: p2r(12);
      }

      a {
        display: flex;
        align-items: center;
        color: $font_white;

        &.active {
          color: $font_accent;
          cursor: default;

          @include tablet {
            color: $font_white;
          }

          .menu-aside-icon {
            filter: brightness(1);

            @include tablet {
              filter: brightness(10);
            }
          }
        }

        @media (hover: hover) and (pointer: fine) {
          &:hover {
            color: $font_accent;

            .menu-aside-icon {
              filter: brightness(1);
            }
          }
        }
      }
    }



    &-icon {
      flex: 0 0 p2r(40);
      width: p2r(40);
      height: p2r(40);
      margin-right: p2r(16);
      filter: brightness(10);
      transition: filter 0.15s;

      @include tablet {
        margin-right: p2r(8);
      }

      @include mobile {
        flex: 0 0 p2r(32);
        width: p2r(32);
        height: p2r(32);
      }
    }

    &-txt {
      padding-right: p2r(8);
    }
  }

  &-main {
    flex-grow: 1;
    overflow-x: hidden;
    overflow-y: auto;
    margin-top: p2r(32);
    margin-bottom: p2r(32);

    &-back {
      font-size: p2r(14);
      line-height: 1;
      opacity: 0.5;
      margin-bottom: p2r(12);
      display: flex;
      align-items: center;
      color: $font_white;

      @include tablet {
        margin-top: p2r(44);
      }

      @include mobile {
        margin-top: p2r(24);
        font-size: p2r(12);
      }

      &::before {
        content: '';
        // content: url('data:image/svg+xml,<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5.09766 8C6.98489 8.30831 10.3065 10.2165 11.0977 14" stroke="white"/><path d="M5.09766 8C6.98489 7.69169 10.3065 5.78351 11.0977 2" stroke="white"/></svg>');
        background-image: url('data:image/svg+xml,<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5.09766 8C6.98489 8.30831 10.3065 10.2165 11.0977 14" stroke="white"/><path d="M5.09766 8C6.98489 7.69169 10.3065 5.78351 11.0977 2" stroke="white"/></svg>');;
        background-repeat: no-repeat;
        background-position: center;
        background-size: p2r(16) p2r(16);
        width: p2r(16);
        height: p2r(16);
        margin-right: p2r(4);

        @include mobile {
          width: p2r(10);
          height: p2r(10);
          background-size: p2r(108) p2r(10);
        }
      }

      &-icon {
        width: p2r(16);
        height: p2r(16);
        flex: 0 0 p2r(16);
        fill: none;
        stroke: $font_white;
        opacity: 0.5;
      }
    }


    &-title {
      font-family: $second_font;
      font-size: p2r(32);
      color: $font_accent;
      font-weight: 400;
      margin-bottom: p2r(32);
      width: 100%;
      flex: 0 0 100%;
    }

    &-section {
      margin-right: p2r(60);
      display: flex;
      flex-wrap: wrap;

      &:not(:last-child) {
       border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        margin-bottom: p2r(40);
        padding-bottom: p2r(32);
      }

      & > ul {
        width: 50%;

        & > li {
          font-size: p2r(20);

          & > a {
            color: $font_white;
            display: block;
            line-height: 1.2;
            padding-top: p2r(6);
            padding-bottom: p2r(6);
            margin-bottom: p2r(4);

            @media (hover: hover) and (pointer: fine) {
              &:hover {
                color: $font_accent;
              }
            }

            &.active {
              color: $font_accent;
            }
          }

          ul {
            & > li {
              a {
                color: $font_white;
                font-size: p2r(16);
                line-height: 1.2;
                opacity: 0.8;
                display: block;
                padding-top: p2r(6);
