      @include tablet {
        padding-top: p2r(13);
        padding-bottom: p2r(13);
      }

      @include mobile {
        padding-top: p2r(15);
        padding-bottom: p2r(14);
      }

      &:first-of-type {
        @include tablet {
          padding-top: 0;
        }
      }

      &:last-of-type {
        border: none;
        padding-bottom: 0;
      }

      &-title {
        font-size: p2r(24);
        line-height: 1.2;
        margin-bottom: p2r(3);

        @include tablet {
          font-size: p2r(20);
        }

        @include mobile {
          font-size: p2r(16);
          margin-bottom: 0;
        }
      }

      &-text {
        font-size: p2r(16);

        @include mobile {
          font-size: p2r(14);
        }
      }
    }
  }

  &-price {
    &-wrapper {
      position: relative;
      max-height: p2r(400);
      overflow: hidden;
      max-width: p2r(690);
      transition: max-height 0.3s;
      cursor: pointer;

      @include tablet {
        max-height: p2r(300);        
      }

      @include mobile {
        max-height: p2r(350);        
      }

      &.show {
        max-height: p2r(2000);
      }

      &.has-overflow:not(.show) {
        &::after {
          position: absolute;
          bottom: p2r(-2);
          left: 0;
          right: 0;
          content: '';
          height: p2r(93);
          background: linear-gradient(0deg, rgb(244, 240, 236) 6%, rgba(255, 255, 255, 0));         

          @include tablet {
            display: none;
          }
      }
      }
    }

    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // max-width: p2r(696);
      padding-top: p2r(15);
      padding-bottom: p2r(15);
      border-bottom: 1px solid rgba(24, 44, 34, 0.1);

      @include tablet {
        padding-top: p2r(13);
        padding-bottom: p2r(13);
      }

      @include mobile {
        padding-top: p2r(12);
        padding-bottom: p2r(12);
      }

      &:first-of-type {
        @include tablet {
          padding-top: 0;
        }
      }

      &:last-of-type {
        border: none;
        padding-bottom: 0;
      }      
    }

    &-title {      
      max-width: p2r(400);   
      line-height: 1.2;
      
      @include tablet {
        max-width: p2r(430);      
        font-size: p2r(16);
      }

      @include mobile {
        max-width: p2r(184);      
        font-size: p2r(14);
      }
    }

    &-value { 
      line-height: 1.2;

      @include tablet {
        font-size: p2r(16);
      }

      @include mobile {
        font-size: p2r(14);
      }

      span {
        color: $font_accent;       
        font-weight: 600;
        font-size: p2r(24);
        line-height: 1.2;

        @include tablet {
          font-size: p2r(16);
        }
      }
    }
  }

  &-picture {
    display: none;
    position: relative;
    margin-top: p2r(-52);
    padding-top: p2r(60);

    @include tablet {
      aspect-ratio: 2.52;
      display: block;
      overflow: hidden;
    }

    @include mobile {
      margin-top: p2r(-58);
      aspect-ratio: 1.23;
    }

    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      aspect-ratio: 2.52;
      object-fit: cover;
      object-position: center top;
      border-radius: p2r(24);
      z-index: 2;

      @include tablet {
        object-position: center;
        border-radius: 0 0 p2r(24) p2r(24);
        filter: brightness(70%);
      }

      @include mobile {
        aspect-ratio: 1.23;
      }
    }
  }

  &-presentation {
    position: relative;
    padding: p2r(32);
    background: #F2E7DC;
    border-radius: p2r(40);
    margin-bottom: p2r(80);
    overflow: hidden;
    margin-top: p2r(22);
    min-height: p2r(300);

    @include tablet {
      padding: 0;
      padding-top: p2r(0);
      margin-top: p2r(12);
      margin-bottom: p2r(56);
      background: none;
      border-radius: 0;
      min-height: unset;
    }

    @include mobile {
      margin-top: p2r(16);
      margin-bottom: p2r(38);
    }

    &-image {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: p2r(901);
      height: 100%;
      object-fit: cover;
      border-top-left-radius: p2r(40);
      border-bottom-left-radius: p2r(40);

      @include tablet {
        display: none;
      }
    }

    &-info {
      width: p2r(493);
      padding: p2r(32);
      background-color: rgba(255, 255, 255, 0.5);
      backdrop-filter: blur(p2r(16));
      border-radius: p2r(24);

      @include tablet {
        width: auto;
        background-color: transparent;
        backdrop-filter: unset;
        padding: 0;
        border-radius: 0;
      }
    }

    &-title {
      font-family: $second_font;
      color: $font_black;
      font-size: p2r(40);
      line-height: 1;
      margin-bottom: p2r(24);

      @include tablet {
        margin-bottom: p2r(16);
        line-height: 0.85;
      }

      @include mobile {
        word-wrap: break-word;
        hyphens: manual;
      }
    }

    &-text {
      margin-bottom: p2r(24);

      @include tablet {
        font-size: p2r(16);
      }

      @include mobile {
        margin-bottom: p2r(16);
      }
    }

    &-appointment {
      @include tablet {
        margin-bottom: p2r(16);
      }

      @include mobile {      
        font-size: 20px;
        letter-spacing: 0.02em;
        padding-left: p2r(24);
        padding-right: p2r(24);
      }
    }
  }
}

