.side {
  &-spase {

    @include tablet {
      margin-bottom: p2r(24);

      &:has(.float) {
        margin-bottom: p2r(47);
      }
    }
  }

  &-menu {
    background-color: rgba(255, 255, 255, 0.8);
    padding: p2r(32) p2r(20);
    border-radius: p2r(24);
    max-width: p2r(184);
    transition: background-color 0.3s;
    width: max-content;
    background-blend-mode: soft-light, normal;
    backdrop-filter: blur(p2r(16));


    @include tablet {
      max-width: 100%;
      background: none;
      background-color: transparent;
      border-radius: 0;
      padding: 0;
      // margin-bottom: p2r(24);
    }

    &.hidden {
      display: none;
    }

    &.float {
      @include tablet {
        left: 0;
        right: 0;
        padding-left: p2r(16);
        padding-right: p2r(16);
        padding-top: p2r(20);
        background-color: $bg_article;
        width: 100%;

        &.black {
          background-color: $primary;

          li {
            color: $font_white;
          }
        }

        &:not(.black) {
          li {
            color: $font_black;
          }         
        }
      }
    }

    &.flip-bottom {
      @include tablet {
        background-color: $bg_article;
        padding-top: p2r(24);
        width: 100%;
        
        li {
          color: $font_black
        }
      }
    }

    li {
      padding-left: p2r(8);
      padding-right: p2r(8);
      line-height: 1.2;
      transition: color 0.3s;

      @include tablet {
        font-size: p2r(16);
        padding-left: 0;
        padding-right: 0;
        padding-bottom: p2r(4);
      }

      @media (hover: hover) and (pointer: fine) {
        &:hover {
          &:not(.active) {
            color: $font_accent;
          }
        }
      }

      &:not(.active) {
        cursor: pointer;
      }

      &:not(:last-child) {
        margin-bottom: p2r(16);

        @include tablet {
          margin-bottom: 0;
          margin-right: p2r(16);
        }
      }

      &.active {
        position: relative;

        &::after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: p2r(2);
          border-radius: p2r(2);
          background-color: #E9B566;

          @include tablet {
            top: auto;
            bottom: 0;
            height: p2r(2);
            width: 100%;
          }
        }
      }
    }
  }

  &-navigation-list {

    @include tablet {
      display: flex;
      padding-left: p2r(16);
      padding-right: p2r(16);
      margin-left: p2r(-16);
      margin-right: p2r(-16);
      white-space: nowrap;
      overflow-x: auto;

      overflow: -moz-scrollbars-none;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        -webkit-appearance: none;
        width: 0;
        height: 0;
        display: none;
      }
    }
  }
}

