        margin-right: p2r(30);

        @include tablet {
          font-size: p2r(16);
        }

        @include mobile {
          font-size: p2r(14);
        }

        a {
          color: $font_white;

          &.active {
            color: $font_accent;
            pointer-events: none;
          }

          @media (hover: hover) and (pointer: fine) {
            &:hover {
              color: $font_accent;
            }
          }
        }

        &:not(:last-of-type) {
          position: relative;

          &::after {
            position: absolute;
            right: p2r(-15);
            top: 50%;
            transform: translate(50%, -50%);
            content: '';
            width: p2r(6);
            height: p2r(6);
            border-radius: 50%;
            background-color: $font_white;

            @include tablet {
              width: p2r(5);
              height: p2r(5);
            }

            @include mobile {
              width: p2r(4);
              height: p2r(4);
            }
          }
        }

        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  &-results {
    margin-bottom: p2r(24);
  }

  &-result {
    border-bottom: 1px solid rgba(233, 181, 102, 0.2);
    padding-top: p2r(32);
    padding-bottom: p2r(32);

    @include tablet {
      padding-top: p2r(16);
      padding-bottom: p2r(16);
    }

    &__min {
      padding-top: p2r(20);
      padding-bottom: p2r(20);
    }

    &:last-of-type {
      border: none;
    }

    &-wrapper {
      display: block;

      @media (hover: hover) and (pointer: fine) {
        &:hover {
          .search-result-head-icon {
            filter: brightness(1);
          }
        }
      }
    }

    .breadcrumbs {
      margin-bottom: p2r(8);
    }

    &-head {
      font-family: $second_font;
      font-size: p2r(32);
      line-height: 0.85;
      color: $font_white;
      // margin-bottom: p2r(16);
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include tablet {
        font-size: p2r(20);
        line-height: 1.10;
      }

      @include mobile {
        font-size: p2r(16);
        line-height: 1.2;
      }

      &-title {
        max-width: p2r(800);

        @include tablet {
          max-width: p2r(555);
        }

        @include mobile {
          max-width: p2r(256);
        }
      }

      span {
        color: $font_accent;
      }

      &-icon {
        width: p2r(46);
        height: p2r(24);
        fill: none;
        transition: filter 0.3s ease-in-out;
        filter: brightness(10);

        @include tablet {
          width: p2r(32);
          height: p2r(16);
        }
      }
    }

    &-text {
      font-size: p2r(16);
      line-height: 1.2;
      color: $font_white;
      max-width: p2r(800);
      margin-top: p2r(16);

      @include tablet {
        max-width: p2r(555);
      }

      @include mobile {
        max-width: p2r(256);
        font-size: p2r(12);
        margin-top: p2r(10);
      }

      span {
        color: $font_accent;
      }
    }
  }

  &-all {
    text-align: center;
    margin-bottom: p2r(48);

    .ui-btn {
      @include mobile {
        padding-left: p2r(12);
        padding-right: p2r(12);
      }
    }
  }

  &-more {
    margin-bottom: p2r(64);

    @include tablet {
      margin-bottom: p2r(40);
    }
    .ui-btn {
      color: $font_white;

      @media (hover: hover) and (pointer: fine) {
        &:hover {
          color: $font_accent;

          .ui-btn-icon {
            stroke: $font_accent;
            filter: unset;
          }
        }
      }

      &-icon {
        width: p2r(24);
        stroke: $font_white;
        stroke-width: p2r(0.5);
        transition: stroke 0.3s;
      }
    }
  }
}

.popup-selection {
