.solutions {
  position: relative;
  z-index: 9;
  margin-bottom: p2r(160);

  @include tablet {
    margin-bottom: p2r(80);
  }

  @include tablet {
    margin-bottom: p2r(60);
  }

  &-wrapper {
   margin-left: auto;
   margin-right: auto;
   max-width: p2r(1206);
  }

  &-title {
    font-family: $second_font;
    font-style: normal;
    font-weight: 400;
    font-size: p2r(72);
    line-height: 0.85;
    color: $font_white;
    margin-bottom: p2r(40);

    @include tablet {
      font-size: p2r(40);
      margin-bottom: p2r(24);
    }

    @include mobile {
      font-size: p2r(32);
      margin-bottom: p2r(24);
    }
  }

  &-cards {
