    width: 100%;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &-card {
    position: relative;
    display: flex;
    flex-direction: column;
    width: calc(25% -  0.9224011713rem);
    height: p2r(400);
    margin-right: p2r(14);
    padding: p2r(20) p2r(16);
    background-color: $primary;
    border-radius: p2r(24);
    transition: width 0.5s ease;
    box-sizing: border-box;
    overflow: hidden;
    cursor: pointer;

    @include tablet {
      width: calc(50% - 0.3513909224rem);
      height: p2r(250);
      margin-right: p2r(8);
      margin-bottom: p2r(8);
      padding: p2r(16);
    }

    @include mobile {
      width: 100%;
      height: p2r(200);
      margin-right: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    &:nth-child(even) {
      @include tablet {
        margin-right: 0;
      }

    }

    &-image {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      height: p2r(400);
      width: 100%;
      object-fit: cover;
      object-position: center;
      z-index: 1;

      @include tablet {
        height: p2r(250);
        object-position: center p2r(-40);
      }

      @include mobile {
        height: p2r(200);
      }
