      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      font-family: $second_font;
      font-weight: 400;
      font-size: p2r(32);
      line-height: 0.85;
      color: $font_white;
      word-break: break-word;

      @include mobile {
        font-size: p2r(24);
      }

      .icon {
        width: p2r(32);
        height: p2r(16);
        fill: none;
        filter: brightness(10);
      }
    }

    &-content {
      overflow: hidden;
      max-height: 0;
      transition: max-height 0.7s,  margin-top 0.7s;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      font-weight: 400;
      font-size: p2r(16);
      line-height: 1.2;

      a {
        position: relative;
        color: $font_white;

        @media (hover: hover) and (pointer: fine) {
          &:hover {
            color: $font_accent;
          }
        }

        &:not(:last-child) {
          &::after {
            position: absolute;
            top: 50%;
            right: p2r(-10);
            transform: translate(50%, -50%);
            content: '';
            width: p2r(4);
            height: p2r(4);
            border-radius: 50%;
            background-color: $accent;
          }
