      }

      &-item {
        margin-right: p2r(20);
      }
    }

    &-more {
      display: flex;
      align-items: center;
      font-family: $second_font;
      font-weight: 400;
      font-size: p2r(24);
      line-height: 0.85;
      letter-spacing: 0.02em;
      color: $font_accent;
      margin-top: p2r(20);

      @media (hover: hover) and (pointer: fine) {
        &:hover {
          .icon {
            filter: brightness(10);
          }
        }
      }

      .icon {
        width: p2r(48);
        height: p2r(24);
        fill: none;
        margin-left: p2r(12);
        transition: color 0.3s, filter 0.15s;
      }
    }
  }
}

