.comparison {
  margin-bottom: p2r(160);

  @include tablet {
    margin-bottom: p2r(80);
  }

  @include mobile {
    margin-bottom: p2r(64);
  }

  &-container {
    position: relative;
    width: 100%;
    height: p2r(680);
    overflow: hidden;
    user-select: none;
    border-radius: p2r(40);
    touch-action: none;
    padding-left: p2r(48);
    padding-right: p2r(48);
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include tablet {
      height: p2r(420);
      border-radius: p2r(24);
      padding: p2r(16);
    }

    @include mobile {
      align-items: flex-end;
    }
  }

  &-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: left top;
    display: block;

    &__after {
      clip-path: inset(0 0 0 50%);
    }
  }

  &-handle {
    position: absolute;
    top: 0;
    bottom: 0;
    // width: 1px;
    // background-color: rgba(233, 181, 102, 0.5);
    left: 50%;
    cursor: ew-resize;
    transform: translateX(-50%);
    width: p2r(10);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 50%;
      width: 1px; /* Визуальная толщина линии */
      background-color: rgba(233, 181, 102, 0.5);
      // transform: translateX(-50%);
    }
  }

  &-label {
    color: $font_accent;
    padding: p2r(8) p2r(12);
    font-size: p2r(14);
    line-height: 1;
    background-color:rgba(16, 51, 43, 0.5);
    backdrop-filter: blur(p2r(16));
    top: 89%;

    @include tablet {
      top: 88%;
    }

    @include mobile {
      top: 4%;
    }

    &__before {
      position: absolute;
      right: 0;
      border-radius: p2r(8) 0 0 p2r(8);

      @include tablet {
        border-radius: p2r(16) 0 0 p2r(16);
      }
    }

    &__after {
      position: absolute;
      left: p2r(5);
      border-radius: 0 p2r(8) p2r(8) 0;
      border-left: 1px solid rgba(233, 181, 102, 0.5);

      @include tablet {
        border-radius: 0 p2r(16) p2r(16) 0;
      }
    }
  }

  &-content {
    position: relative;
    z-index: 2;
    max-width: p2r(561);

    @include tablet {
      width: p2r(249);
    }

    &-title {
      font-family: $second_font;
      font-size: p2r(88);
      line-height: 0.85;
      margin-bottom: p2r(40);

      @include tablet {
        font-size: p2r(40);
        margin-bottom: p2r(16);
      }

      @include mobile {
        font-size: p2r(32);
      }
    }
  }
}

